 import '../index.css';
 import * as localizedMessages from '../__localization__';
 import {
   ActionButton,
   Button,
   ButtonGroup,
   Content,
   Dialog,
   DialogTrigger,
   Divider,
   Flex,
   Heading,
 } from '@adobe/react-spectrum';
 import {
   Cell,
   Counter,
   FiltersOptions,
   Inventory,
   PageHeader,
   Row,
   Search,
   SortOption,
   Table,
   Title,
   ViewHeader,
 } from '@quarry/inventory';
 import React, { useState } from 'react';
 import { DataItem } from '../types';
 import { IntlProvider } from '@quarry/intl';
 import VCCLineChartProto from './VCCLineChart';
 // import { action } from '@storybook/addon-actions';
 import camelCase from 'lodash/camelCase';
 import data from '../data.json';
 import groupBy from 'lodash/groupBy';

 interface LineChartProps {
   /** Locale used to internationalize the component. */
   locale?: string;
   colors?: string[];
   toolTipAccessors: string[];
   toolTipTitle: string[];
   width?: number;
   viewTakeaways?: boolean;
   statisticalNotes?: boolean;
 }

 interface Fields {
   entertainment?: number;
   handSanitizer?: number;
   breadFlour?: number;
   alcohol?: number;
 }

 interface InventoryItem extends Fields {
   key: string;
   month: number;
 }

 interface InventoryColumn {
   key: string;
   label: 'Date' | 'Hand sanitizer' | 'Entertainment' | 'Bread flour' | 'Alcohol';
 }

 const inventoryData = Object.values(groupBy(data, 'month')).map(
   (dataByDate: DataItem[]): InventoryItem => {
     const { key, month } = dataByDate[0];
     const fields: Fields = dataByDate.reduce((acc: { [x: string]: number }, cur) => {
       const { series = '', value } = cur;
       acc[camelCase(series)] = value;
       return acc;
     }, {});

     return {
       key: key.toString(),
       month: month as number,
       ...fields,
     };
   },
 );

 const columns: InventoryColumn[] = [
   {
     key: 'month',
     label: 'Date',
   },
   {
     key: 'handSanitizer',
     label: 'Hand sanitizer',
   },
   {
     key: 'breadFlour',
     label: 'Bread flour',
   },
   {
     key: 'entertainment',
     label: 'Entertainment',
   },
   {
     key: 'alcohol',
     label: 'Alcohol',
   },
 ];

 const sortOptions: SortOption[] = columns.map(({ key: column, label }) => ({
   column,
   label,
   ...(column === 'month' && { direction: 'descending' }),
 }));

 function LineChartComponent(props: LineChartProps): JSX.Element {
   const [items, setItems] = useState<InventoryItem[]>(inventoryData);

   const handleFilterChange = ({ query, sortDescriptor }: FiltersOptions) => {
     if (sortDescriptor) {
       const { column } = sortDescriptor;

       inventoryData.sort((a, b) => {
         let [first, second]: [number | string, number | string] = ['', ''];

         switch (column) {
           case 'month':
             [first, second] = [a.month, b.month];
             break;
           case 'handSanitizer':
             [first, second] = [a.handSanitizer || 0, b.handSanitizer || 0];
             break;
           case 'breadFlour':
             [first, second] = [a.breadFlour || 0, b.breadFlour || 0];
             break;
           case 'entertainment':
             [first, second] = [a.entertainment || 0, b.entertainment || 0];
             break;
           case 'alcohol':
             [first, second] = [a.alcohol || 0, b.alcohol || 0];
             break;
           default:
             break;
         }

         return sortDescriptor.direction === 'descending'
           ? first > second
             ? 1
             : -1
           : first > second
           ? -1
           : 1;
       });
     }
     if (query) {
       setItems(
         inventoryData.filter(
           item => !query || (item.month && `march ${item.month}`.includes(query.toLowerCase())),
         ),
       );
     }
     // action('onFilterChange')({ query });
   };

   const { viewTakeaways, ...otherProps } = props;

   return (
     <div>
       <div id="line-chart">
         <VCCLineChartProto {...props} />
       </div>
       {viewTakeaways && (
         <DialogTrigger type="fullscreen">
           <ActionButton>View Takeaways</ActionButton>
           {close => (
             <Dialog>
               <Heading>Line Chart Insights</Heading>
               <Divider />
               <Content>
                 <Flex alignItems="start" gap="size-800">
                   <div className="instory-text">
                     <ul>
                       <li className="bullet-point">
                         The lowest spend in one day was on March 31st for bread flour - 1.62 million dollars.
                       </li>
                       <li className="bullet-point">
                         The highest spend in one day was on March 24th for hand sanitizer - 7.63 million dollars.
                       </li>
                       <li className="bullet-point">
                         The average daily spend for this month for any of these items was 3.86 million dollars.
                       </li>
                       <li className="bullet-point">
                         The period from March 21st to March 24th showed the most notable increase in daily spending for hand sanitizer. Daily spending for hand sanitizer was 2.52 million dollars on March 1st, then it rose steadily to 5.70 million on March 21st and increased to 7.63 million dollars on March 24th.
                       </li>
                       <li className="bullet-point">
                         The period from March 24th to March 31st showed the most notable drop in daily spending for hand sanitizer. Daily spending for hand sanitizer was 7.63 million dollars on March 24th and 3.83 million dollars on March 31st.
                       </li>
                     </ul>
                   </div>
                   <div className="dialog-graph">
                     <VCCLineChartProto
                       {...{
                         props,
                         ...otherProps,
                         width: 1075,
                         height: 600,
                         statisticalNotes: false,
                       }}
                     />
                   </div>
                 </Flex>
               </Content>
               <ButtonGroup>
                 <Button variant="secondary" onPress={close}>
                   Close
                 </Button>
               </ButtonGroup>
             </Dialog>
           )}
         </DialogTrigger>
       )}
       <div>
         <Inventory
           items={items}
           onFilterChange={handleFilterChange}
           sortOptions={sortOptions}
           height="440px"
         >
           <PageHeader>
             <Title>Data Table</Title>
           </PageHeader>
           <ViewHeader>
             <Search />
             <Counter />
           </ViewHeader>
           <Table columns={columns} selectionMode="single">
             {/* eslint-disable-next-line @typescript-eslint/no-explicit-any */}
             {(data: any) => {
               return (
                 <Row key={data.value}>
                   {column => {
                     return (
                       <Cell>
                         {column === 'month' && 'March '}
                         {data[column]}
                       </Cell>
                     );
                   }}
                 </Row>
               );
             }}
           </Table>
         </Inventory>
       </div>
     </div>
   );
 }

 /**
  * A prototype for Spectrum-ifying Visa Chart Components
  */
 function LineChart(props: LineChartProps): JSX.Element {
   const { locale, ...otherProps } = props;

   return (
     <IntlProvider locale={locale} messages={localizedMessages}>
       <LineChartComponent {...otherProps} />
     </IntlProvider>
   );
 }

 export default LineChart;
