import { DataItem } from './types';
import groupBy from 'lodash/groupBy';
import sortBy from 'lodash/sortBy';

interface DataProps {
  values: DataItem[];
}

export function processData(props: DataProps): DataItem[] {
  const groups = groupBy(sortBy(props.values, ['series', 'key']), 'series');

  Object.keys(groups).forEach(key => {
    difference(groups[key]);
    minMaxValue(groups[key]);
    formatToolTipValue(groups[key]);
  });
  return sortBy(props.values, ['series', 'month']);
}

function formatToolTipValue(props: DataItem[]) {
  Object.values(props).forEach(entry => {
    entry.toolTipValue = entry.value.toFixed(1);
  });
}

function difference(props: DataItem[]) {
  let prevValue: DataItem;
  Object.values(props).forEach(entry => {
    if (entry.key === 0) {
      entry.differenceFromLast = 0;
      prevValue = entry;
    } else if (entry.key === 30) {
      entry.differenceFromNext = 0;
      getPreviousDifference(entry, entry.value - prevValue.value);
    } else {
      getPreviousDifference(entry, entry.value - prevValue.value);
    }
    getNextDifference(prevValue, prevValue.value - entry.value);

    prevValue = entry;
  });
}

function getPreviousDifference(entry: DataItem, difference: number) {
  const absoluteValue = Math.abs(difference);
  const isNegative = difference < 0;
  entry.differenceFromLast = '';

  if (absoluteValue < 0.3) {
    entry.differenceFromLast = `a little ${isNegative ? 'lower' : 'higher'}`;
  } else if (absoluteValue > 0.3 && absoluteValue < 0.8) {
    entry.differenceFromLast = `${isNegative ? 'lower' : 'higher'}`;
  } else {
    entry.differenceFromLast = `much ${isNegative ? 'lower' : 'higher'}`;
  }
}

function getNextDifference(entry: DataItem, difference: number) {
  if (entry.key === 30) {
    entry.differenceFromNext = 0;
    return;
  }
  const absoluteValue = Math.abs(difference);
  const isNegative = difference < 0;
  entry.differenceFromNext = '';

  if (absoluteValue < 0.3) {
    entry.differenceFromNext = `a little ${isNegative ? 'lower' : 'higher'}`;
  } else if (absoluteValue > 0.3 && absoluteValue < 0.8) {
    entry.differenceFromNext = `${isNegative ? 'lower' : 'higher'}`;
  } else {
    entry.differenceFromNext = `much ${isNegative ? 'lower' : 'higher'}`;
  }
}

function minMaxValue(props: DataItem[]) {
  const values = Object.values(props).map(entry => {
    return entry.value;
  });

  const min = Math.min(...values);
  const max = Math.max(...values);

  Object.values(props).forEach(entry => {
    if (entry.value === min) {
      entry.note = '';
      entry.minMax = 'minimum';
      entry.note += 'This is the minimum data point in the series.';
    } else if (entry.value === max) {
      entry.note = '';
      entry.minMax = 'maximum';
      entry.note += 'This is the maximum data point in the series.';
    } else {
      entry.minMax = '';
    }
  });
}

export default processData;
