import * as localizedMessages from '../__localization__';
import React, { useState } from 'react';
import { defineMessages, useIntl } from 'react-intl';
import { DataItem } from '../types';
import { IntlProvider } from '@quarry/intl';
import { LineChart as VCCLineChart } from '@visa/charts-react';
import data from '../data.json';
import processData from '../processData';

const messages = defineMessages({
  month: {
    id: 'line-chart.month',
    defaultMessage: 'March 2020',
  },
  percent: {
    id: 'line-chart.percent',
    defaultMessage: 'U.S. Dollars in millions',
  },
});

interface VCCLineChartProps {
  /** Locale used to internationalize the component. */
  locale?: string;
  colors?: string[];
  toolTipAccessors: string[];
  toolTipTitle: string[];
  width?: number;
  height?: number;
  statisticalNotes?: boolean;
}

function VCCLineChartComponent(props: VCCLineChartProps): JSX.Element {
  const { formatMessage } = useIntl();

  const [hoveredElement, setHoveredElement] = useState({});
  const [showDots, setShowDots] = useState(false);
  const handleHover = ({ detail }: CustomEvent) => {
    setHoveredElement(detail);
    setShowDots(true);
  };

  const handleMouseOut = () => {
    setHoveredElement({});
    setShowDots(false);
  };

  const processedData = processData({ values: data as DataItem[] });

  const {
    statisticalNotes = true,
    colors,
    toolTipAccessors = [],
    toolTipTitle = [],
    width = 1075,
    height = 600,
  } = props;

  return (
    <VCCLineChart
      colors={colors}
      data={processedData}
      showDots={showDots}
      hoverStyle={{
        color: '#000',
        strokeWidth: 5,
      }}
      onHoverFunc={handleHover}
      onMouseOutFunc={handleMouseOut}
      hoverHighlight={hoveredElement}
      dotRadius={1}
      strokeWidth={3}
      dataLabel={{ labelAccessor: 'toolTipValue', visible: showDots, placement: 'top' }}
      legend={{
        visible: false,
        labels: ['Montly Values'],
      }}
      mainTitle="U.S. Consumer Online Spending Habits During The Pandemic"
      padding={{
        top: 0,
        bottom: 65,
        right: 100,
        left: 80,
      }}
      seriesLabel={{
        visible: true,
        placement: 'right',
      }}
      showTooltip={toolTipAccessors.length > 0}
      tooltipLabel={{
        labelAccessor: toolTipAccessors,
        labelTitle: toolTipTitle,
        format: [],
      }}
      subTitle="Mock data showing changes in online purchases in 4 categories during March, 2020."
      uniqueID="my-line-chart"
      xAxis={{
        visible: true,
        label: formatMessage(messages.month),
      }}
      showBaselineX={false}
      yAxis={{
        gridVisible: true,
        visible: true,
        label: formatMessage(messages.percent),
      }}
      ordinalAccessor={'month'}
      accessibility={
        statisticalNotes
          ? {
              executiveSummary:
                'These data show that online spending varied significantly during March 2020 by type, requiring businesses to adapt their marketing plans to account for the impacts of social media, news, and global events. ',
              statisticalNotes:
                'The lowest spend in one day was on March 31st for bread flour - 1.62 million dollars. The highest spend in one day was on March 24th for hand sanitizer - 7.63 million dollars. The average daily spend for this month for any of these items was 3.86 million dollars. The period from March 21st to March 24th showed the most notable increase in daily spending for hand sanitizer. Daily spending for hand sanitizer was 2.52 million dollars on March 1st, then it rose steadily to 5.70 million on March 21st and increased to 7.63 million dollars on March 24th. The period from March 24th to March 31st showed the most notable drop in daily spending for hand sanitizer. Daily spending for hand sanitizer was 7.63 million dollars on March 24th and 3.83 million dollars on March 31st.',
              purpose:
                'The purpose of this chart is to provide users with several ways to analyze data as it relates to the increase in spending in the United States during the March of the pandemic. ',
              longDescription:
                'This line chart shows online shopping trends during March 2020, the first month of the COVID-19 pandemic, using 4 different types of purchases: entertainment, alcohol, hand sanitizer, and bread flour. The chart shows that entertainment ranked highest in initial spending among these 4 categories and remained steady throughout the month. Alcohol purchases began the month as the second highest category of consumer spending, and it remained consistent with a few drops during the month of March. Consumers purchases of hand sanitizer fluctuated significantly over the month of March with sharp increases in spending shown during the middle of the month, surpassing even entertainment and alcohol in overall spend, dropping sharply near the end of the month. And, finally, bread flour purchases represented the lowest spend in the 4 categories across the entire month of March, remaining relatively constant with only a few deviations',
              structureNotes:
                'This chart has 4 separate lines displaying several points of data over one month. The Y axis measures spending amounts (in U.S. dollars), and the X axis marks each day from March 1, 2020 to March 31, 2020. Below the chart, there is a data table with the same information presented in tabular format. There is also a button entitled “Key takeaways” under the X-axis of the chart that displays additional details and insights in a popup window (dialog). ',
              elementsAreInterface: true,
              hideTextures: true,
              hideDataTableButton: true,
              disableValidation: true,
              includeDataKeyNames: true,
              elementDescriptionAccessor: 'note',
            }
          : {
              executiveSummary:
                'These data show that online spending varied significantly during March 2020 by type, requiring businesses to adapt their marketing plans to account for the impacts of social media, news, and global events. ',
              purpose:
                'The purpose of this chart is to provide users with several ways to analyze data as it relates to the increase in spending in the United States during the March of the pandemic. ',
              longDescription:
                'This line chart shows online shopping trends during March 2020, the first month of the COVID-19 pandemic, using 4 different types of purchases: entertainment, alcohol, hand sanitizer, and bread flour. The chart shows that entertainment ranked highest in initial spending among these 4 categories and remained steady throughout the month. Alcohol purchases began the month as the second highest category of consumer spending, and it remained consistent with a few drops during the month of March. Consumers purchases of hand sanitizer fluctuated significantly over the month of March with sharp increases in spending shown during the middle of the month, surpassing even entertainment and alcohol in overall spend, dropping sharply near the end of the month. And, finally, bread flour purchases represented the lowest spend in the 4 categories across the entire month of March, remaining relatively constant with only a few deviations',
              structureNotes:
                'This chart has 4 separate lines displaying several points of data over one month. The Y axis measures spending amounts (in U.S. dollars), and the X axis marks each day from March 1, 2020 to March 31, 2020. Below the chart, there is a data table with the same information presented in tabular format. There is also a button entitled “Key takeaways” under the X-axis of the chart that displays additional details and insights in a popup window (dialog). ',
              elementsAreInterface: true,
              hideTextures: true,
              hideDataTableButton: true,
              disableValidation: true,
              includeDataKeyNames: true,
              elementDescriptionAccessor: 'note',
            }
      }
      width={width}
      height={height}
    />
  );
}

/**
 * A prototype for Spectrum-ifying Visa Chart Components
 */
function VCCLineChartProto(props: VCCLineChartProps): JSX.Element {
  const { locale, ...otherProps } = props;

  return (
    <IntlProvider locale={locale} messages={localizedMessages}>
      <VCCLineChartComponent {...otherProps} />
    </IntlProvider>
  );
}

export default VCCLineChartProto;
